import React from "react";
import "./CyberSportGoals.css";

const CyberSportGoals = () => {
	const sections = [
		{
			icon: "",
			title: "Rivojlanish",
			description:
				"Ozbekistonda kibersportni raqobatbardosh faoliyatning yangi turi sifatida rivojlantirishni qollab-quvvatlash.",
		},
		{
			icon: "",
			title: "Turnirlar",
			description:
				"Ozbekistonda milliy va xalqaro kibersport musobaqalarini tashkil etish.",
		},
		{
			icon: "",
			title: "Xalqaro aloqalar",
			description:
				"Ozbekistonning jahon kibersport musobaqalarida ishtirokini qollab-quvvatlash.",
		},
		{
			icon: "",
			title: "Me'yoriy hujjatlar",
			description:
				"Sport unvonlari va darajalarini berish boyicha talablar va standartlarni ishlab chiqish.",
		},
		{
			icon: "",
			title: "Hamkorlik",
			description:
				"Kibersportni rivojlantirish bilan shugullanayotgan klublar va muassasalarni qollab-quvvatlash.",
		},
		{
			icon: "",
			title: "Tadbirlar",
			description:
				"Forumlar, tanlovlar, seminarlar va turli ommaviy tadbirlarni otkazish.",
		},
	];


	return (
		<div id="turlar" style={{ backgroundColor: "#000", padding: "2rem", color: "#fff", paddingTop: "4rem" }}>
			<h2 style={{ textAlign: "center", fontSize: "1.8rem", marginBottom: "2rem" }}>
				 Kibersportni rivojlantirish va targib qilish maqsadlari
			</h2>

			<div className="cybersport-grid">
				{sections.map((section, idx) => (
					<div className="cybersport-card" key={idx}>
						<h3 style={{ fontSize: "1.1rem", marginBottom: "0.5rem", color: "#ffc107" }}>
							{section.icon} <span style={{ color: "#fff" }}>{section.title}</span>
						</h3>
						<p style={{ fontSize: "0.95rem", lineHeight: "1.5", color: "#ddd" }}>
							{section.description}
						</p>
					</div>
				))}
			</div>
		</div>
	);
};

export default CyberSportGoals;
